import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { Book, Medium, SearchCriteria } from './types';
import './Hero.css';
import { HeroButton, HeroLinkButton } from './Buttons';
import { TopNReleases } from './TopReleases';
import banner from './images/hero-banner.jpg';
import heroStyles from './Hero.module.css';
import appStyles from './App.module.css';

type OtherStuffProps = {
  genres: string[];
  tags: string[];
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
  books: Book[];
};

function OtherStuff({
  genres,
  tags,
  searchCriteria,
  setSearchCriteria,
  books,
}: OtherStuffProps) {
  const history = useHistory();

  function browseAllBooks() {
    // TODO: Use Book context function clearSearch()
    setSearchCriteria({
      genres: [],
      media: [],
      searchText: '',
      tags: [],
    });

    history.push('/search');
  }
  return (
    <>
      <TopNReleases count={15} books={books} />
      <HeroLinkButton onClick={browseAllBooks}>Browse All Books</HeroLinkButton>
      <HeroSearch
        searchCriteria={searchCriteria}
        setSearchCriteria={setSearchCriteria}
      />
      <BrowseByGenre genres={genres} setSearchCriteria={setSearchCriteria} />
      <BrowseByTag tags={tags} setSearchCriteria={setSearchCriteria} />
      <BrowseByMedium setSearchCriteria={setSearchCriteria} />
    </>
  );
}

type HeroBannerProps = {
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

export function HeroBanner({ setSearchCriteria }: HeroBannerProps) {
  const history = useHistory();

  function browseAllBooks() {
    // TODO: Use Book context function clearSearch()
    setSearchCriteria({
      genres: [],
      media: [],
      searchText: '',
      tags: [],
    });

    history.push('/search');
  }

  return (
    <div className={heroStyles['banner-container']}>
      <div className={`${appStyles.container} ${heroStyles.banner}`}>
        <div>
          <h1>Buy Books Directly from the Author!</h1>
          <p>
            Put 90%+ straight into your favorite author's pocket.
          </p>
          <p className={heroStyles['hero-buttons']}>
            <HeroButton onClick={browseAllBooks}>
              <i className="fas fa-book"></i> See What's New
            </HeroButton>
          </p>
        </div>
        <img
          className={heroStyles['banner-image']}
          src={banner}
          alt="A formidable multi-story library"
        />
      </div>
    </div>
  );
}

type HeroSearchProps = {
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function HeroSearch({ searchCriteria, setSearchCriteria }: HeroSearchProps) {
  return (
    <>
      <h2>Search by Title or Author</h2>
      <HeroSearchToolbar
        searchCriteria={searchCriteria}
        setSearchCriteria={setSearchCriteria}
      />
    </>
  );
}

type BrowseByGenreProps = {
  genres: string[];
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function BrowseByGenre({ genres, setSearchCriteria }: BrowseByGenreProps) {
  const history = useHistory();

  function browseByGenre(genre: string) {
    setSearchCriteria({
      searchText: '',
      genres: [genre],
      tags: [],
      media: [],
    });
    history.push('/search');
  }

  return (
    <>
      <h2>Browse by Genre</h2>
      <ul className="hero-list">
        {genres.map((g) => {
          return (
            <li key={g} className="genre-link">
              <HeroLinkButton onClick={() => browseByGenre(g)}>
                {g}
              </HeroLinkButton>
            </li>
          );
        })}
      </ul>
    </>
  );
}

type BrowseByTagProps = {
  tags: string[];
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function BrowseByTag({ tags, setSearchCriteria }: BrowseByTagProps) {
  const history = useHistory();

  function browseByTag(tag: string) {
    setSearchCriteria({
      searchText: '',
      genres: [],
      tags: [tag],
      media: [],
    });
    history.push('/search');
  }

  return (
    <>
      <h2>Browse by Tags</h2>
      <ul className="hero-list">
        {tags.map((t) => {
          return (
            <li key={t} className="tag-link">
              <HeroLinkButton onClick={() => browseByTag(t)}>
                {t}
              </HeroLinkButton>
            </li>
          );
        })}
      </ul>
    </>
  );
}

type BrowseByMediumProps = {
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function BrowseByMedium({ setSearchCriteria }: BrowseByMediumProps) {
  const history = useHistory();

  function browseByMedium(medium: Medium) {
    setSearchCriteria({
      searchText: '',
      genres: [],
      tags: [],
      media: [medium],
    });
    history.push('/search');
  }

  return (
    <>
      <h2>Browse by Medium</h2>
      <ul className="hero-list">
        <li>
          <HeroLinkButton onClick={() => browseByMedium('Physical')}>
            Physical
          </HeroLinkButton>
        </li>
        <li>
          <HeroLinkButton onClick={() => browseByMedium('eBook')}>
            eBook
          </HeroLinkButton>
        </li>
        <li>
          <HeroLinkButton onClick={() => browseByMedium('Audiobook')}>
            Audiobook
          </HeroLinkButton>
        </li>
      </ul>
    </>
  );
}

type HeroSearchToolbarProps = {
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

export function HeroSearchToolbar({
  searchCriteria,
  setSearchCriteria,
}: HeroSearchToolbarProps) {
  const history = useHistory();
  const [searchText, setSearchText] = useState(searchCriteria.searchText ?? '');

  function search(event: React.FormEvent) {
    event.preventDefault();
    const newCriteria = {
      ...searchCriteria,
      searchText: searchText,
    };
    setSearchCriteria(newCriteria);
    history.push('/search');
  }

  return (
    <form onSubmit={search} className="search hero">
      <div className="search-control">
        <input
          type="text"
          name="Search"
          id="search"
          className="search"
          placeholder="Search..."
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
        <button className="search-button">
          <i className="fas fa-search"></i>
        </button>
      </div>
    </form>
  );
}
