import { Book } from './types';
import styles from './Books.module.css';

type BookGalleryProps = {
  books: Book[];
};

export function BookGallery({ books }: BookGalleryProps) {
  return (
    <div className={styles.gallery}>
      {books.map((book: Book) => (
        <BookSummary
          key={book.Title + book.AuthorFullName + book.PublicationDate}
          book={book}
        />
      ))}
    </div>
  );
}

type BookSummaryProps = {
  book: Book;
};

function BookSummary({ book }: BookSummaryProps) {
  return (
    <article
      key={book.Title + book.AuthorFullName + book.PublicationDate}
      className={styles['book-summary']}
    >
      <a
        href={book.Link}
        target="_blank"
        rel="noreferrer"
        className={styles['image-link']}
      >
        <img
          src={book.ImageSrc}
          alt={`Book cover of "${book.Title}" by ${book.AuthorFullName}`}
        />
      </a>
      <p>
        <b>{book.Title}</b>
        <br />
        <i>{book.AuthorFullName}</i>
      </p>
    </article>
  );
}
