import React, { useEffect, useState } from 'react';
import '../node_modules/@fortawesome/fontawesome-free/css/all.min.css';
import './App.css';
import { Search } from './Search';
import { HeroBanner } from './Hero';
import { sampleData } from './sample-data';
import { Book, Medium, SearchCriteria } from './types';
import { Switch, Route, BrowserRouter as Router, Link } from 'react-router-dom';

function App() {
  const [books, setBooks] = useState<Book[]>([]);
  const [searchCriteria, setSearchCriteria] = useState<SearchCriteria>({
    searchText: '',
    genres: [],
    tags: [],
    media: [],
  });
  const [genres, setGenres] = useState<string[]>([]);
  const [tags, setTags] = useState<string[]>([]);
  const [authors, setAuthors] = useState<string[]>([]);

  useEffect(() => {
    let publications = [...sampleData];

    const today = Date.now();

    publications.sort((a, b) => {
      let aDate = Date.parse(a.DateAdded);
      if (isNaN(aDate)) {
        aDate = today;
      }
      let bDate = Date.parse(b.DateAdded);
      if (isNaN(bDate)) {
        bDate = today;
      }
      return aDate > bDate ? -1 : aDate < bDate ? 1 : 0;
    });

    setBooks(publications);
  }, []);

  useEffect(() => {
    console.log('Initializing genres');

    const genreSet = new Set<string>();
    const tagSet = new Set<string>();
    const authorSet = new Set<string>();
    books.forEach((curr) => {
      genreSet.add(curr.Genre);
      curr.Tags.forEach((t) => tagSet.add(t));
      authorSet.add(curr.AuthorFullName);
      return genreSet;
    });

    setGenres(
      Array.from(genreSet).sort((a: string, b: string) =>
        a > b ? 1 : a < b ? -1 : 0
      )
    );

    setTags(
      Array.from(tagSet).sort((a: string, b: string) =>
        a > b ? 1 : a < b ? -1 : 0
      )
    );

    setAuthors(
      Array.from(authorSet).sort((a: string, b: string) =>
        a > b ? 1 : a < b ? -1 : 0
      )
    );
  }, [books]);

  function searchByMedium(medium: Medium) {
    setSearchCriteria({
      searchText: '',
      genres: [],
      tags: [],
      media: [medium],
    });
  }

  function clearSearch() {
    setSearchCriteria({
      searchText: '',
      genres: [],
      tags: [],
      media: [],
    });
  }

  return (
    <Router basename="/buy-books-direct">
      <header className="header">
        <div className="content container">
          <h1 className="brand">
            <Link to="/" onClick={clearSearch}>
              buy books direct <i className="fas fa-book"></i>
            </Link>
          </h1>
          <input type="checkbox" id="nav-toggle-checkbox" />
          <label htmlFor="nav-toggle-checkbox" className="nav-toggle">
            <i className="fas fa-bars"></i>
          </label>
          <nav className="nav">
            <Link className="nav-link" to="/" onClick={clearSearch}>
              Home
            </Link>
            <Link
              className="nav-link"
              to="/search"
              onClick={() => searchByMedium('eBook')}
            >
              eBooks
            </Link>
            <Link
              className="nav-link"
              to="/search"
              onClick={() => searchByMedium('Audiobook')}
            >
              Audiobooks
            </Link>
            <span className="nav-link">Get Added to this List</span>
            <span className="nav-link">Subscribe to this List</span>
            <Link className="nav-link pointer" to="/search" title="Search">
              <i className="fas fa-search"></i>
            </Link>
          </nav>
        </div>
      </header>
      <main>
        <Switch>
          <Route path="/" exact={true}>
            <HeroBanner setSearchCriteria={setSearchCriteria} />
          </Route>
          <Route path={['/search']}>
            <div className="container">
              <Search
                books={books}
                searchCriteria={searchCriteria}
                setSearchCriteria={setSearchCriteria}
                tags={tags}
                genres={genres}
                authors={authors}
              />
            </div>
          </Route>
        </Switch>
      </main>
      <footer className="container">
        This is a footer. Craig, what're putting down here?
      </footer>
    </Router>
  );
}

export default App;
