import { useEffect, useState } from 'react';
import { LinkButton } from './Buttons';
import { Medium, SearchCriteria } from './types';

type FilterOptionAttributes<T> = {
  value: T;
  checked: boolean;
};

type FilterOptionProps = {
  checked: boolean;
  value: string;
  onChange: React.ChangeEventHandler<HTMLInputElement>;
};

function FilterOption({ checked, onChange, value }: FilterOptionProps) {
  return (
    <>
      <label className={`filter-option ${checked ? 'checked' : ''}`}>
        <input
          type="checkbox"
          value={value}
          checked={checked}
          onChange={onChange}
          style={{ display: 'none' }}
        />
        {value}
      </label>
    </>
  );
}

type GenreFilterProps = {
  genres: string[];
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function GenreFilter({
  genres,
  searchCriteria,
  setSearchCriteria,
}: GenreFilterProps) {
  const [genreOptions, setGenreOptions] = useState<
    FilterOptionAttributes<string>[]
  >([]);

  useEffect(() => {
    const genreFilterData = genres.map<FilterOptionAttributes<string>>(
      (genre) => {
        return {
          value: genre,
          checked: searchCriteria.genres.includes(genre),
        };
      }
    );

    setGenreOptions(genreFilterData);
  }, [genres, searchCriteria.genres]);

  const onGenreChanged: React.ChangeEventHandler<HTMLInputElement> = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const updatedGenres = genreOptions
      .map((c) =>
        c.value !== event.target.value
          ? { ...c }
          : { ...c, checked: !c.checked }
      )
      .filter((t) => t.checked)
      .map((t) => t.value);

    setSearchCriteria({ ...searchCriteria, genres: updatedGenres });
  };

  return (
    <>
      <h3 className="filter">Filter by Genre</h3>
      {genreOptions.map((g) => (
        <FilterOption
          key={g.value}
          checked={g.checked}
          value={g.value}
          onChange={onGenreChanged}
        />
      ))}
    </>
  );
}

type TagFilterProps = {
  tags: string[];
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function TagFilter({
  tags,
  searchCriteria,
  setSearchCriteria,
}: TagFilterProps) {
  const [tagOptions, setTagOptions] = useState<
    FilterOptionAttributes<string>[]
  >([]);

  useEffect(() => {
    const tagFilterData = tags.map<FilterOptionAttributes<string>>((tag) => {
      return {
        value: tag,
        checked: searchCriteria.tags.includes(tag),
      };
    });

    setTagOptions(tagFilterData);
  }, [tags, searchCriteria.tags]);

  const onTagChanged: React.ChangeEventHandler<HTMLInputElement> = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const updatedTags = tagOptions
      .map((c) =>
        c.value !== event.target.value
          ? { ...c }
          : { ...c, checked: !c.checked }
      )
      .filter((t) => t.checked)
      .map((t) => t.value);

    setSearchCriteria({ ...searchCriteria, tags: updatedTags });
  };

  return (
    <>
      <h3 className="filter">Filter by Tag</h3>
      {tagOptions.map((t) => (
        <FilterOption
          key={t.value}
          checked={t.checked}
          value={t.value}
          onChange={onTagChanged}
        />
      ))}
    </>
  );
}

type MediumFilterProps = {
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

function MediumFilter({
  searchCriteria,
  setSearchCriteria,
}: MediumFilterProps) {
  const [mediaOptions, setMediaOptions] = useState<
    FilterOptionAttributes<Medium>[]
  >([]);

  useEffect(() => {
    const media: Medium[] = ['Audiobook', 'eBook', 'Physical'];
    
    const mediaFilterData = media.map<FilterOptionAttributes<Medium>>(
      (medium: Medium) => {
        return {
          value: medium,
          checked: searchCriteria.media.includes(medium),
        };
      }
    );

    setMediaOptions(mediaFilterData);
  }, [searchCriteria.media]);

  const onMediumChanged: React.ChangeEventHandler<HTMLInputElement> = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    const updatedMedia = mediaOptions
      .map((c) =>
        c.value !== event.target.value
          ? { ...c }
          : { ...c, checked: !c.checked }
      )
      .filter((m) => m.checked)
      .map((m) => m.value);

    setSearchCriteria({ ...searchCriteria, media: updatedMedia });
  };

  return (
    <>
      <h3 className="filter">Filter by Medium</h3>
      {mediaOptions.map((m) => (
        <FilterOption
          key={m.value}
          checked={m.checked}
          value={m.value}
          onChange={onMediumChanged}
        />
      ))}
    </>
  );
}

type SearchFilterProps = {
  tags: string[];
  genres: string[];
  authors: string[];
  searchCriteria: SearchCriteria;
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

export function SearchFilters({
  tags,
  genres,
  authors,
  searchCriteria,
  setSearchCriteria,
}: SearchFilterProps) {
  function searchAuthor(authorFullName: string) {
    setSearchCriteria((c) => {
      return { ...c, searchText: authorFullName };
    });
  }

  return (
    <>
      <div>
        <GenreFilter
          genres={genres}
          searchCriteria={searchCriteria}
          setSearchCriteria={setSearchCriteria}
        />
      </div>
      <div>
        <TagFilter
          tags={tags}
          searchCriteria={searchCriteria}
          setSearchCriteria={setSearchCriteria}
        />
      </div>
      <div>
        <MediumFilter
          searchCriteria={searchCriteria}
          setSearchCriteria={setSearchCriteria}
        />
      </div>
      <div>
        <h3 className="filter">Authors</h3>
        {authors.map((a) => (
          <LinkButton
            key={a}
            className="author"
            onClick={() => searchAuthor(a)}
          >
            {a}
          </LinkButton>
        ))}
      </div>
    </>
  );
}
