import React from 'react';
import styles from './Buttons.module.css';

type LinkButtonProps = React.ButtonHTMLAttributes<unknown>;

export function LinkButton(props: LinkButtonProps) {
  const { children, className, ...remainingAttributes } = props;
  return (
    <button
      className={`${styles['link-button']} ${className ?? ''}`}
      {...remainingAttributes}
    >
      {children}
    </button>
  );
}

type HeroLinkButtonProps = React.ButtonHTMLAttributes<unknown>;

export function HeroLinkButton(props: HeroLinkButtonProps) {
  const { children, className, ...remainingAttributes } = props;
  return (
    <button
      className={`${styles['hero-link-button']} ${className ?? ''}`}
      {...remainingAttributes}
    >
      {children}
    </button>
  );
}

type HeroButtonProps = React.ButtonHTMLAttributes<unknown>;

export function HeroButton(props: HeroButtonProps) {
  const { children, ...rest } = props;
  return <button className={styles['hero-button']} {...rest}>{props.children}</button>;
}
